<template>
  <div  class="modal fade"
        id="editModel"
        tabindex="-1"
        role="dialog"
        aria-labelledby="editModellLabel"
        aria-hidden="true">
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="editModellLabel">
            {{isnew?"":` ${tempData.title} `}}
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <validation-observer v-slot="{ invalid }" ref="form">
          <form @submit.prevent="Edit">
            <div class="modal-body">
              <div class="row">
                  <div class="col-sm-4">
                      <div class="form-group">
                        <button type="button"
                                class="btn btn-primary btn-sm mr-2"
                                @click="AddImage()"></button>
                        <button type="button"
                                class="btn btn-primary btn-sm"
                                @click="UploadImage()"></button>
                        <input  id="customFile"
                                ref="file"
                                type="file"
                                class="d-none form-control"
                                @change="UploadFile">
                      </div>
                      <template  v-for="(item, key) in tempData.imageUrl">
                          <div class="form-group" :key="key">
                              <label :for="'imageUrl'+key"></label>
                              <button type="button"
                                      class="btn btn-sm btn-outline-danger"
                                      @click="DelImage(key)">
                                      <i class="fa fa-trash"></i>
                              </button>
                              <input  type="text"
                                      class="form-control"
                                      :id="'imageUrl'+key"
                                      placeholder=""
                                      v-model.trim="tempData.imageUrl[key]">
                              <img :src="tempData.imageUrl[key]"
                                alt="" class="img-fluid">
                          </div>
                      </template>
                  </div>
                  <div class="col-sm-8">
                      <div class="form-group">
                        <validation-provider name="" rules="required"
                          v-slot="{ errors, classes}">
                          <label for="productsTitle"></label>
                          <input  type="text"
                                  class="form-control"
                                  id="productsTitle"
                                  placeholder=""
                                  v-model="tempData.title"
                                  :class="classes">
                          <!--  -->
                          <span class="invalid-feedback">{{ errors[0] }}</span>
                        </validation-provider>
                      </div>
                      <div class="form-row">
                          <div class="form-group col-md-6">
                            <validation-provider name="" rules="required"
                              v-slot="{ errors, classes}">
                              <label for="productsCategory"></label>
                              <input  type="text"
                                      class="form-control"
                                      id="productsCategory"
                                      placeholder=""
                                      v-model="tempData.category"
                                      :class="classes">
                              <!--  -->
                              <span class="invalid-feedback">{{ errors[0] }}</span>
                            </validation-provider>
                          </div>
                          <div class="form-group col-md-6">
                              <validation-provider name="" rules="required"
                              v-slot="{ errors, classes}">
                              <label for="productsUnit"></label>
                              <input  type="text"
                                      class="form-control"
                                      id="productsUnit"
                                      placeholder=""
                                      v-model="tempData.unit"
                                      :class="classes">
                              <!--  -->
                              <span class="invalid-feedback">{{ errors[0] }}</span>
                              </validation-provider>
                          </div>
                      </div>
                      <div class="form-row">
                          <div class="form-group col-md-6">
                            <validation-provider name="" rules="required"
                              v-slot="{ errors, classes}">
                              <label for="origin_price"></label>
                              <input  type="number"
                                      class="form-control"
                                      id="origin_price"
                                      placeholder=""
                                      v-model="tempData.origin_price"
                                      :class="classes">
                              <!--  -->
                              <span class="invalid-feedback">{{ errors[0] }}</span>
                              </validation-provider>
                          </div>
                          <div class="form-group col-md-6">
                            <validation-provider name="" rules="required"
                              v-slot="{ errors, classes}">
                              <label for="productsPrice"></label>
                              <input  type="number"
                                      class="form-control"
                                      id="productsPrice"
                                      placeholder=""
                                      v-model="tempData.price"
                                      :class="classes">
                              <!--  -->
                              <span class="invalid-feedback">{{ errors[0] }}</span>
                              </validation-provider>
                          </div>
                      </div>
                      <div class="form-group">
                          <validation-provider name="" rules="required"
                              v-slot="{ errors, classes}">
                          <label for="productsDescription"></label>
                          <textarea type="text"
                                    id="productsDescription"
                                    placeholder=""
                                    class="form-control"
                                    v-model="tempData.description"
                                    :class="classes"></textarea>
                          <!--  -->
                          <span class="invalid-feedback">{{ errors[0] }}</span>
                          </validation-provider>
                      </div>
                      <div class="form-group">
                        <validation-provider name="" rules="required"
                          v-slot="{ errors, classes}">
                          <label for="productsContent"></label>
                          <vue-editor
                            id="content"
                            v-model="tempData.content"
                            :class="classes" />
                          <!--  -->
                          <span class="invalid-feedback">{{ errors[0] }}</span>
                          </validation-provider>
                      </div>
                      <div class="form-group">
                          <div class="form-check">
                              <input  type="checkbox"
                                      id="productsEnabled"
                                      class="form-check-input"
                                      v-model="tempData.enabled">
                              <label  class="form-check-label"
                                      for="productsEnabled"></label>
                          </div>
                      </div>
                  </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
              <button type="submit" class="btn btn-primary" :disabled="invalid"></button>
            </div>
          </form>
        </validation-observer>
      </div>
    </div>
  </div>
</template>

<script>
/* global $ */
import { VueEditor } from 'vue2-editor/dist/vue2-editor.core';

export default {
  components: {
    VueEditor,
  },
  props: {
    pruduct: {
      type: Object,
    },
  },
  data() {
    return {
      tempData: {
        imageUrl: [],
      },
      isnew: Boolean,
    };
  },
  methods: {
    getProduct(id = 0) {
      this.$refs.form.reset();
      if (id === 0) {
        this.isnew = true;
        this.tempData = { imageUrl: [] };
        $('#editModel').modal('show');
      } else {
        this.isnew = false;
        const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${id}`;

        this.$bus.$emit('setLoading');

        this.$http.get(url).then((response) => {
          this.tempData = response.data.data;
          $('#editModel').modal('show');
        }).catch(() => {
        }).finally(() => {
          this.$bus.$emit('setLoading');
        });
      }
    },
    Edit() {
      let url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product`;
      let type = 'post';
      if (!this.isnew) {
        url += `/${this.tempData.id}`;
        type = 'patch';
      }

      this.$bus.$emit('setLoading');
      const checkImg = this.tempData.imageUrl.filter((item) => item.length > 0);
      if (checkImg.length === 0) {
        this.$toast.error('');
        this.$bus.$emit('setLoading');
      } else {
        this.$http[type](url, this.tempData).then(() => {
          this.$emit('updateProducts');
          this.$toast.success(` ${this.tempData.title} ${this.isnew ? '' : ''}`);
          $('#editModel').modal('hide');
        }).catch(() => {
        }).finally(() => {
          this.$bus.$emit('setLoading');
        });
      }
    },
    AddImage() {
      if (this.tempData.imageUrl.length < 5) {
        this.tempData.imageUrl.push('');
      } else {
        this.$toast.error('');
      }
    },
    DelImage(index) {
      this.tempData.imageUrl.splice(index, 1);
    },
    UploadImage() {
      const index = this.tempData.imageUrl.findIndex((item) => item.length === 0);
      if (this.tempData.imageUrl.length < 5 || index >= 0) {
        this.$refs.file.dispatchEvent(new MouseEvent('click')); //  refs 
      } else {
        this.$toast.error('');
      }
    },
    UploadFile() {
      const uploadedFile = this.$refs.file.files[0];
      const formData = new FormData();
      formData.append('file', uploadedFile);
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/storage`;
      this.$bus.$emit('setLoading');
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((response) => {
        if (response.status === 200) {
          const index = this.tempData.imageUrl.findIndex((item) => item.length === 0);
          if (index >= 0) {
            this.$set(this.tempData.imageUrl, index, response.data.data.path);
          } else {
            this.tempData.imageUrl.push(response.data.data.path);
          }
          this.$toast.success('');
        }
      }).catch(() => {
        this.$toast.error(' 2MB');
      }).finally(() => {
        this.$bus.$emit('setLoading');
      });
    },
  },
};
</script>

