<template>
    <div>
      <div class="text-right mb-3">
        <button type="button" class="btn btn-primary" @click="Action('Add')"> </button>
      </div>
      <table class="table">
        <thead class="thead-dark">
          <tr>
            <th width="100"></th>
            <th ></th>
            <th width="100"></th>
            <th width="100"></th>
            <th width="100"></th>
            <th width="150"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item) in products" :key="item.id">
            <th scope="row">{{item.category}}</th>
            <td>{{item.title}}</td>
            <td class="text-right">{{item.origin_price}}</td>
            <td class="text-right">{{item.price}}</td>
            <td :class="!item.enabled ? 'text-danger' :'text-success' ">
              {{item.enabled ? '':''}}
            </td>
            <td><div class="btn-group" role="group" aria-label="Basic example">
                <button type="button" class="btn btn-outline-primary btn-sm"
                @click="Action('Edit',item)"></button>
                <button type="button" class="btn btn-outline-danger btn-sm"
                @click="Action('Delete',item)"></button>
              </div></td>
          </tr>
        </tbody>
      </table>
       <!--  -->
       <Pagination
        :pagination="pagination"
        @emitPages="getProducts" />
      <!--  -->
      <DelProductModel
        ref='delProductModel'
        :pruduct="tempData"
        @emitPages="getProducts" />
      <!-- / -->
      <ProductModel
        ref='productModel'
        :pruduct="tempData"
        @updateProducts="getProducts" />
  </div>
</template>

<script>
/* global $ */
import Pagination from '@/components/Pagination.vue';
import DelProductModel from '@/components/dashboard/DelProductModel.vue';
import ProductModel from '@/components/dashboard/ProductModel.vue';

export default {
  components: {
    Pagination,
    DelProductModel,
    ProductModel,
  },
  data() {
    return {
      products: [],
      pagination: {},
      tempData: {},
    };
  },
  created() {
    this.getProducts();
  },
  methods: {
    getProducts(page = 1) {
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/products?page=${page}&paged=10`;
      this.$bus.$emit('setLoading');
      this.$http.get(url).then((result) => {
        this.products = result.data.data;
        this.pagination = result.data.meta.pagination;
      }).catch(() => {
      }).finally(() => {
        this.$bus.$emit('setLoading');
      });
    },
    Action(action, item = {}) { // 
      this.tempData = item;
      switch (action) {
        case 'Add': // 
          this.isNew = true;
          //  refs 
          this.$refs.productModel.getProduct(); // id
          break;
        case 'Edit': // 
          this.isNew = false;
          //  refs 
          this.$refs.productModel.getProduct(item.id); // ID
          break;
        case 'Delete': // 
          $('#delProductModel').modal('show');
          break;
        default:
          break;
      }
    },
  },
};
</script>
