<template>
  <div  class="modal fade" id="delProductModel"
        tabindex="-1" role="dialog" aria-labelledby="delProductModelLabel" aria-hidden="true">
        <div class="modal-dialog">
            <div class="modal-content rounded border-0">
                <div class="modal-header bg-danger text-white">
                <h5 class="modal-title" id="delProductModelLabel"></h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
                </div>
                <div class="modal-body">
                     <strong class="text-danger">{{pruduct.title}}</strong> 
                </div>
                <div class="modal-footer">
                <button type="button"
                        class="btn btn-outline-secondary" data-dismiss="modal"></button>
                <button type="button"
                        class="btn btn-danger" @click="Delete()">
                        
                </button>
              </div>
          </div>
        </div>
    </div>
</template>

<script>
/* global $ */
export default {
  props: {
    pruduct: {
      type: Object,
    },
  },
  data() {
    return {
    };
  },
  methods: {
    Delete() {
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/product/${this.pruduct.id}`;
      this.$bus.$emit('setLoading');
      this.$http.delete(url).then(() => {
        $('#delProductModel').modal('hide');
        this.$emit('emitPages');
        this.$toast.success(`${this.pruduct.title}`);
      }).catch(() => {
        this.$toast.error(`${this.pruduct.title}`);
      }).finally(() => {
        this.$bus.$emit('setLoading');
      });
    },
  },
};
</script>
